* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.content {
  /* height: 100vh; */
}

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

/* Transaction page */
.filters {
  display: flex;
  box-shadow: 0 0 3px gray;
  justify-content: space-between;
  align-items: center;
}

/* Transaction page */

/* Ant D icons  */
.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: #000000;
}

.inactive-icon {
  color: rgba(128, 128, 128, 0.557);
}

.chart-box {
  border: 2px solid #0000006f;
  border-radius: 5px;
  padding: 10px 15px;
}

